<template>
  <div class="search">
      <div class="top">
        <input type="text" v-model="query" autofocus placeholder="请输入电影名称" @keyup.enter="search(query)"/>
        <span @click="go(-1)">取消</span>
      </div>
      <div class="hot">
        <p>热门电影</p>
        <router-link v-for="item in hotSearch" :to="'/home/movie/'+ item.id" :key="item.id" tag="button">{{item.name}}</router-link>
      </div>
      <div class="kira">
        <p>每日一句</p>
        <div>
            你什么都不是~~~~~~！！！！
        </div>
      </div>
  </div>
</template>

<script>
    export default {
        name: 'search',
        mounted () {
            this.$store.dispatch('hideNav');
            this.$store.dispatch('hideFoot');
        },
        data () {
            return {
                query: '',
                hotSearch: [
                    {name: '金刚狼3', id: '25765735'},
                    {name: '一条狗的使命', id: '6873143'},
                    {name: '生化危机:终章', id: '20471852'},
                    {name: '乐高蝙蝠侠', id: '26145033'},
                    {name: '爱乐之城', id: '25934014'},
                    {name: '欢乐好声音', id: '26354572'}
                ]
            }
        },
        methods: {
            go: function(n){
                this.$router.go(n);
            },
            search: function(n){
                if(n === ''){
                    return
                }
                this.$router.push({path: '/home/search/result',query: {name : n}});
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
   .top {
       display: flex;
       height: 30px;
       padding: 10px;
   }
   .top input {
       flex: 1;
       border: none;
       border-bottom: 1px solid #ccc;
       padding: 10px;
       outline: none;
       font-size: 14px;
       text-indent: 20px;
   }
   .top span {
       padding: 0 10px;
       font-size: 17px;
       line-height: 30px;
   }
   .hot {
       padding: 10px;
       margin-top: 20px;
   }
   .hot p {
       font-size: 20px;
       color: hotpink;
       margin-bottom: 20px;
   }
   .hot button {
       padding: 0 20px;
       line-height: 25px;
       margin:3px 10px;
       outline: none;
       background: #FFF;
       border: 1px lightpink solid;
       color: deeppink;
       border-radius: 17px;

   }
   .kira {
       margin-left: 20px;
       margin-top: 300px;
   }
   .kira p{
        font-size: 17px;
        color: deeppink;
   }
   .kira div {
       font-size: 14px;
       margin-top: 20px;

   }
</style>


